.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 10px;
  box-shadow: 0px 0px 3px 1px rgb(255, 119, 0);
  width: 250px;
  height: 350px;
  overflow: hidden;
  border-radius: 4px;
  margin:auto;
  color: rgb(255, 119, 0);
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  padding: 20px 0;
}

.avatar {
  width: auto;
  height: 100px;
  border-radius: 50%;
}

.name {
  font-size: 25px;
  font-weight: 700;
  margin: 20px 0 0 0;
}

.name,
.tag {
  margin: 20px 0 0 0;
}

.tag,
.location {
  font-weight: 600;
  font-size: 14px;
  color: rgb(255, 119, 0);
}

.stats {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 0.5px solid rgb(151, 163, 175);
  width: 100%;
  padding: 15px 0;
  background-color: rgb(240, 244, 247);
}

.label {
  padding-bottom: 2px;
  font-size: 11px;
  color: rgb(151, 163, 175);
  font-weight: 600;
}

.quantity {
  font-size: 12px;
  font-weight: 700;
}